.App {
  text-align: center;
  background-color: #222;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.App-link {
  color: #61dafb;
}

.form-horizontal {
  width: 40% !important;
  margin-left: 30%
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.image {
  width: 300px;
}

.review-form {
  margin-left: 10%;
}

#text-field {
  width: 35%;
}
.submit-btn {
  margin-left: -50px;
}
.form-group.row {
  margin-left: auto !important;
  padding-left: 0;
}
.col-sm-1 {
  padding-left: 0 !important;
}

.dropdown-toggle {
  margin-left: 0% !important;
  font-weight: 700;
}
.form-label {
  position: absolute;
  left: 2%;
  font-weight: 700;
  margin-left: 0 !important;
}